@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
}

* { font-family: 'Spoqa Han Sans Neo', 'sans-serif'; }


html, body, #root {
  height: 100%;
}

#root {
  display: flex;
  flex-wrap: nowrap;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

$dividerColor: rgb(50, 50, 50);

#lua-sidebar {
  left: 0;
  overflow-y: scroll;
  height: 100%;
  background-color: rgb(29, 29, 29);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  border-right: 1px solid $dividerColor;
  transition: all 0.2s;
  width: 240px;

  & > #lua-sidebar-body {
    width: 100%;
  }

  &::-webkit-scrollbar {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  #lua-sidebar {
    width: 0px;
  }
  .lua-wrapper {
    width: 100% !important;
    padding-left: 10px !important;
  }
  .lua-container {
    padding-left: 10px !important;
  }
}

h1,h2,h3,h4,h5,h6 {
  font-weight: 500;
}

.lua-divider {
  background-color: $dividerColor;
  &.vertical {
    height: 100%;
    width: 1px;
  }
  &.horizontal {
    width: 100%;
    height: 1px;
  }
  &.margin {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

.lua-list {
  padding: 7px;
  transition: all .2s;
  
  & > .lua-listitem-container {
    padding-bottom: 0px;
    padding-top: 0px;
    display: block;
    position: relative;
    width: 100%;
  }

  &.background {
    background-color: rgba(20, 20, 20, 0.5);
    padding: 0px;
    height: 0px;
    overflow: hidden;
  }

  &::-webkit-scrollbar {
    display: none;
  }
}

.lua-button {
  padding: 7px 30px 7px 30px;
  width: 100%;
  outline: none;
  border-radius: 3px;
  background: transparent;
  border: none;
  color: white;
  transition: 0.2s all;
  font-weight: 400;
  font-size: 15px;
  position: relative;
  overflow: hidden;

  &:hover {
    background: rgba(255, 255, 255, 0.12);
  }
}

.lua-ripple {
  width: 100%;
}

.lua-container {
  position: relative;
  color: white;
  padding: 10px;
  padding-left: 50px;
  max-height: 100%;
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

.lua-small {
  font-size: 13px;
  color: white;
}

.lua-info {
  font-size: 13px;
  color: rgb(75, 75, 75)
}

.lua-rows {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-top: 10px;
  border-radius: 3px;
  padding: 10px;
}

.lua-column {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
  border-radius: 3px;
  padding: 10px;
}

.lua-card {
  background-color: rgb(35, 35, 35);
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin: 0 auto;
  gap: 0;
  &.fullwidth {
    width: 100%;
  }
  flex: 1 0 calc(50% - 10px);
}

.lua-warning {
  display: block;
  background-color: rgba(255, 0, 0, 0.3);
  color: white;
  padding: 0.5rem;
  border-radius: 3px;
  margin: 10px;
  margin-left: 3px;
  margin-right: 3px;
}

.lua-topbar {
  top: 0;
  padding: 5px;
  background-color: rgb(45, 45, 45);
  border-radius: 3px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.lua-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  color: white;
  padding: 10px;
  padding-left: 50px;
  max-height: 100%;
  overflow-y: auto;
  width: calc(100% - 160px - 240px);
}
